<template>

    <div class="header container">
        <template v-if="$route.name === 'article' || $route.name === 'note' ">
           <div class="nav-item nav-menu nav-front">
                <template v-for="(repo, key) in github.repos">
                    <div :index="key" class="nav-menu-item" v-bind:class="key === github.repoKey ? 'curr': ''" v-on:click="switchRepo(key, repo)" v-if="repo.scope === 'public' || user.admin">
                        <span class="nav-menu-label">
                        {{repo.name}} <span class="logo iconfont icon-jiesuo" v-if="repo && repo.scope === 'private'"></span>
                        </span>
                    </div>
                </template>
            </div>
        </template>

        <template v-if="github.repo && github.repo.type === 'article' ">
            <template v-if="$route.name === 'article'">
                <div class="nav-item">
                    <form role="search" class="nav-form" v-on:submit.prevent="search">
                        <input type="search" maxlength="32" placeholder="关键字" class="nav-input"
                               v-model="keyword"></input>
                        <a class="nav-icon iconfont icon-sousuo" v-on:click="search" ></a>
                    </form>
                </div>
            </template>
        </template>

        <div class="nav-item nav-control">
            <template v-if="$route.name === 'editor' && user.admin">
                <div class="nav-item">
                    <form role="search" class="nav-form" v-on:submit.prevent>
                        <input v-bind:readonly="file.sha" type="search" maxlength="32" placeholder="文件名称" class="nav-input"
                               v-model="path">.md
                        <a v-on:click="saveFile" class="nav-icon iconfont icon-baocun"> </a>
                        <a v-on:click="discardFile" class="nav-icon iconfont icon-chexiao"></a>
                        <a v-if="file.sha" v-on:click="removeFile" class="nav-icon iconfont icon-shanchu"></a>
                    </form>
                </div>
            </template>

            <div class="nav-item">
                <div class="nav-menu">
                    <div class="nav-menu-item" title="设为默认" v-if="defaultRepoKey() !== github.repoKey">
                        <span class="nav-menu-label" v-on:click="resumeRepo(github.repoKey, true)">
                            <a class="nav-icon iconfont icon-ding" ></a>
                        </span>
                    </div>
                    <template v-if="github.repo && github.repo.type === 'article' && $route.name === 'article' && user.admin">
                        <div class="nav-menu-item" title="新建文章">
                            <span class="nav-menu-label">
                                <a v-on:click="newFile" class="nav-icon iconfont icon-jiahao"></a>
                            </span>
                        </div>
                        <div class="nav-menu-item" v-if="file.sha" title="修改文章">
                            <span class="nav-menu-label">
                                <a v-on:click="editFile" class="nav-icon iconfont icon-bianjibiaoge" ></a>
                            </span>
                        </div>
                    </template>
                    <div class="nav-menu-item"  v-bind:title="user.login && ('@'+ user.login) || 'github登录'"
                         @mouseover="loginHover = true"
                         @mouseleave="loginHover = false">
                        <span class="nav-menu-label">
                            <a v-if="user.login"  class="nav-icon iconfont " v-on:click="logout()"
                               v-bind:class="{'icon-gerentouxiang': !loginHover && !user.admin, 'icon-guanliyuanrenzheng':  !loginHover && user.admin, 'icon-dengchu': loginHover}"></a>
                            <a v-else @click.exact="goLogin(false)" @click.ctrl.exact="goLogin(true)" class="nav-icon iconfont icon-saomafuquan" ></a>
                        </span>
                    </div>
                </div>

            </div>
        </div>
    </div>


</template>

<script>
    import {api, config} from '../github';
    import {mapMutations, mapState} from 'vuex'


    export default {
        name: "Header",
        computed: {
            ...mapState([
                'file',
                'dir',
                'user',
                'token'
            ])
        },
        data() {
            return {
                processing: false,
                loginHover: false,
                keyword: null,
                path: null,
                github: config,
            }
        },
        mounted() {
            this.initial();
        },
        watch: {
            "$route"(from, to) {
                this.initial();
            },
            path(newValue, prevValue){
                if(!/^\w*(\w+\/?){0,2}\w*$/.test(newValue)){
                    this.$nextTick(() => {
                        this.path = prevValue;
                    });
                }
            }
        },
        methods: {
            ...mapMutations([
                "receiveFile",
                "receiveDir",
                "receiveUser",
                "receiveRepo",
                "receiveToken"
            ]),
            initial() {
                if(this.$route.name === 'editor') {
                    //编辑文件状态
                    if (this.$route.params.sha) {
                        this.file.sha = this.$route.params.sha;
                        this.file.path = this.$route.params.path || '';
                        this.path = this.file.path.replace(/\.md$/, '');
                    } else{
                        //新建文件状态
                        this.path = this.dir && (this.dir + "/newfile") || "newfile";
                        this.receiveDir(this.$route.params.mkdir || "");
                    }
                }

                this.keyword = this.$route.params.keyword;
                this.resumeRepo(this.$route.params.repo);
                this.resumeUser(this.$route.query.access_token, !!this.$route.query.admin);
            },
            switchRepo(key, repo){
                this.receiveFile({}); //清楚狀態
                return this.$router.push(api.repoIndexPath(key));
            },
            search() {
                return this.$router.push({
                    name: "search",
                    params: {
                        repo: config.repoKey,
                        keyword: this.keyword
                    }
                });
            },
            defaultRepoKey(){
                return this.$cookies.get("repo") || api.defaultRepoKey();
            },
            resumeRepo(repoKey, markedDefault) {
                if(!repoKey){
                    let repoKey = this.$cookies.get("repo");

                    if(!repoKey){
                        repoKey = api.defaultRepoKey();
                        this.$cookies.set("repo", repoKey, Infinity);
                    }
                }

                this.receiveRepo(repoKey);

                if(markedDefault){
                    this.$cookies.set("repo", repoKey, Infinity);
                    this.$forceUpdate();
                }
            },
            resumeUser(token, admin) {
                let prevToken = this.$cookies.get("token");
                let user = this.$cookies.get("user") || {};

                if (token && prevToken !== token) {
                    this.$cookies.set("token", token, "7d");
                    this.receiveToken(token);

                    api.user().then(promise => {
                        user.login = promise.data.login;
                        user.avatar = promise.data.avatar_url + "&s=24";
                        user.admin = admin;
                        this.$cookies.set("user", JSON.stringify(user), "7d");
                        this.receiveUser(user);
                    })
                }else{
                    this.receiveToken(prevToken);
                    this.receiveUser(user);
                }

            },
            goHome(callback) {
                return this.$router.push(api.routePath(this.file.path || this.dir));
            },
            goLogin(me) {
                return this.$router.push({
                    name: "login",
                    query: {
                        me: me,
                        redirectUrl: window.location.origin + window.location.pathname + "?admin=" + (me || '')
                    }
                });
            },
            logout() {
                this.$cookies.remove("token"); //清除sessionStorage
                this.$cookies.remove('user');//清除用户信息
                this.receiveUser({});
                this.receiveToken(null);
                this.$forceUpdate();
            },
            newFile() {
                this.receiveFile({}); //清楚狀態
                return this.$router.push("/add/" + config.repoKey + '/' + this.dir)
            },
            editFile() {
                return this.$router.push("/edit/"+  config.repoKey + '/' + this.file.sha + "/" + this.file.path)
            },
            removeFile() {
                if (this.processing) {
                    return;
                }
                this.processing = "remove";
                api.remove(this.file).then(promise => {
                    this.receiveFile({});
                    return this.goHome();
                }).finally(() => {
                    this.processing = false;
                });
            },
            saveFile() {
                if (this.processing) {
                    return;
                }
                this.processing = "save";
                if (!this.file.sha) {
                    this.file.path = this.path + '.md';
                }
                api.commit(this.file).then(promise => {
                    return this.goHome();
                }).finally(() => {
                    this.processing = false;
                });
            },
            discardFile() {
                this.goHome();
            }
        }
    }
</script>



<style scoped >

    .header {
        display: flex;
    }

    .nav-item {
        display: flex;
        align-items: center;
        flex-grow: 0;
        line-height: 4rem;
    }

    .nav-menu {
        display: flex;
    }

    .nav-menu-item {
        position: relative;
        cursor: pointer;
        padding: 0 1rem ;

    }

    .nav-front .nav-menu-item {
        font-size: 1.2rem;
        font-weight: bolder;
    }

    .nav-menu-item:hover {
        color: #1989fa;
    }

    .nav-menu-item:hover:after {
        transform: scale(1);
    }

    .nav-menu-item.curr {
        color: #1989fa;
    }

    .nav-menu-item.curr:after {
        transform: scale(1);
    }

    .nav-menu-item:after {
        content: "";
        display: inline-block;
        position: absolute;
        bottom: 0;
        left: .25rem;
        width:  calc(100% - .5rem);
        height: 2px;
        background: #1989fa;
        transform: scale(0);
        transition: all .3s;
    }

    .nav-menu-label {
        white-space: nowrap;
    }

    .nav-menu-label:after {
        position: absolute;
        content: '';
        margin-top: 1rem;
        margin-left: 1rem;
        width: 1px;
        height: 2rem;
        background-color: #ebebeb;
    }

    .nav-form {
        border: 1px solid #fff;
        background-color: rgba(227, 231, 236, .2);
        line-height: 2rem;
        margin-left: 1rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 2px;
    }

    .nav-form .nav-input {
        flex: 1 1 auto;
        justify-content: flex-end;
        border: none;
        width: 10rem;
        padding: .6rem 0 .6rem 1rem;
        box-shadow: none;
        outline: none;
        font-size: 1rem;
        background-color: transparent;

    }

    .nav-form .nav-icon {
        padding-left: .5rem;
    }

    .nav-form .nav-icon:last-child {
        padding-right: .5rem;
    }

    .nav-form .nav-icon:hover {
        cursor: pointer;
        color: #1989fa;
    }

    .nav-form:focus-within {
        border-color: #0366d6;
        background-color: #fff;
    }

    .nav-control {
        flex-grow: 1;
        justify-content: flex-end;
    }

    .header .iconfont {
        font-size: 1.4rem !important;
    }


</style>
