import Vue from 'vue'
import Router from 'vue-router'

import {markdownPathReg, notePathReg, api} from './github';

import Home from './views/Home.vue';
import Editor from './views/Editor.vue';
import SearchPanel from './views/SearchPanel.vue';
import ArticlePanel from './views/ArticlePanel.vue';
import NotePanel from './views/NotePanel.vue';
import Login from './views/Login.vue';

Vue.use(Router);

function getCookie(name) {
  let value = "; " + document.cookie;
  let parts = value.split("; " + name + "=");
  if (parts.length === 2) return parts.pop().split(";").shift();
}

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
        path: '/',
        redirect: api.repoIndexPath(getCookie("repo") || api.defaultRepoKey())
    },
    {
      path: '/',
      component: Home,
      children: [
        {
          name: 'note',
          path: '/note/:repo(' + notePathReg + ')',
          component: NotePanel
        },
        {
          name: 'article',
          path: '/article/:repo(' + markdownPathReg + ')/:path*',
          component: ArticlePanel
        },
        {
          path: '/edit/:repo(' + markdownPathReg + ')/:sha/:path*',
          component: Editor,
          name: 'editor'
        },
        {
          path: '/add/:repo(' + markdownPathReg + ')/:mkdir*',
          component: Editor,
          name: 'editor'
        },
        {
          name: 'search',
          path: '/search/:repo(' + markdownPathReg + ')/:keyword',
          component: SearchPanel
        },
        {
          name: 'login',
          path: '/login',
          component: Login
        }
      ]
    }
  ]
})
