<template>
    <div class="container fluid-container">

        <div class="panel entry-list" >
            <DirectoryPanel v-bind:loading="loading" v-bind:items="items" v-bind:path="path"></DirectoryPanel>
        </div>

        <div class="panel entry-body">
            <template v-if="!preview">
                <span>{{loading && "加载中..." || "markdown预览"}}</span>
            </template>
            <template v-else>
                <ArticleBodyPanel v-bind:preview="preview" v-bind:repo="repo" v-bind:path="path"></ArticleBodyPanel>
            </template>
        </div>

    </div>
</template>

<script>

    import { api } from '../github';
    import DirectoryPanel from "./DirectoryPanel";
    import { mapMutations, mapState } from 'vuex'
    import ArticleBodyPanel from "./ArticleBodyPanel";

    export default {
        name: 'ArticlePanel',
        components: {
            ArticleBodyPanel,
            DirectoryPanel
        },
        props: {
            "path": String,
            "repo": String
        },
        data() {
            return {
                loading: false,
                items: null,
                preview: null
            }
        },
        computed: {
            ...mapState([
                'dir',
                'file'
            ])
        },
        created() {
        },
        beforeDestroy() {
        },
        mounted() {
            this.refresh(this.path).then(() => {
                this.restoreHash();
            });
        },
        watch: {
            path(newValue, prevValue){
                if(this.$route.name === 'article'){
                    this.refresh(newValue);
                }
            },
            repo(newValue, prevValue){
                if(this.$route.name === 'article'){
                    this.preview = null;
                    this.items = null;
                    this.refresh(this.path, true);
                }
            }
        },
        methods: {
            ...mapMutations([
                'receiveFile',
                'receiveDir'
            ]),
            refresh(path, updateDir){
                return this.showContent(path || '', updateDir)
            },
            restoreHash(){
                if(!this.$route.hash){
                    return ;
                }

                let anchor = document.querySelector("[href='" + decodeURIComponent(this.$route.hash) + "']");
                if(anchor){
                    let headerOffset = 16 * 5;
                    let elementPosition = anchor.getBoundingClientRect().top;
                    let offsetPosition = elementPosition - headerOffset;

                    window.scrollTo({
                        top: offsetPosition,
                        behavior: "smooth"
                    });
                }
            },
            markedItem(path){
                this.receiveFile(this.items.find(it =>{
                    return it.path === path;
                }));
            },
            showContent(path, updateDir) {
                let dir = null;
                this.loading = true;
                return api.contents(path).then((promise) => {
                    if(promise.type === 'file'){
                        this.preview = promise.data;
                        dir = api.parentDir(path);
                        //目录树需要更新
                        if(updateDir || dir !== this.dir || this.items === null) {
                            return this.showContent(dir).then((dirPromise) => {
                                this.markedItem(path);
                                return dirPromise;
                            })
                        }
                        else {
                            this.markedItem(path);
                        }
                    }
                    else {
                        this.items = promise.data;
                        dir = path;
                    }
                    this.receiveDir(dir);
                    return promise;
                }).catch(reason => {
                    this.preview = reason.message;
                    if(updateDir){
                        return this.showContent(api.parentDir(path));
                    }
                }).finally(() => {
                    this.loading = false;
                });
            }
        }
    }

</script>



<style scoped>
    @import '~github-markdown-css/github-markdown.css';


    .entry-list {
        position: sticky;
        top: 5rem;
    }

    .entry-list {
        flex: 1;
        box-sizing: border-box;
        box-shadow: 0 2px 10px rgba(0,0,0,.08);
    }

    .entry-body {
        flex: 4;
        box-sizing: border-box;
        box-shadow: 0 2px 10px rgba(0,0,0,.08);
        margin: 1rem 0 2rem 1rem;
        padding: 1rem 1rem 1rem 2rem;
        overflow: hidden;
    }

    .panel {
        background: #fff;
        overflow: hidden;
    }


</style>
